* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(68, 241, 52);
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

button {
  margin: 5rem 0;
  padding: 1rem 2rem;
  outline: none;
  border: none;
  background-color: white;
  font-size: 1.2rem;
  text-transform: uppercase;
  border-radius: 0.5rem;
  cursor: pointer;
  box-shadow: 0 0 0 4px rgb(9, 194, 39);
  color: rgb(15, 97, 7);
}
